import {memberInfo, memberUpdate, uploadImage} from '@/api/userInfo';
const graceChecker = require('@/common/graceChecker.js');
import avatar from '@/components/rf-avatar/rf-avatar';
import moment from '@/utils/moment';
export default {
	components: { avatar },
	data() {
		return {
			userInfo:{},
			loadProgress: 0,
			CustomBar: this.CustomBar,
			profileInfo: {},
			avatar: null,
			genders: [
				
				{
					value: '0',
					name: '男'
				},
				{
					value: '1',
					name: '女'
				}],
			date: moment().format('YYYY-MM-DD'),
			token: null,
			loading: true
		};
	},
	onLoad () {
		this.initData()
	},
	methods: {
		
		toAdministrator(){
			 this.$api.msg('当前信息不可修改，请联系管理员');
		},
		// 上传头像
		uploadImage () {
			// 从相册选择图片
			const _this = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				sourceType: ['album'],
				success: function(res) {
					_this.handleUploadFile(res.tempFilePaths);
				}
			});
		},
		// 上传头像
		handleUploadFile (data) {
			const _this = this;
	    let filePath = data.path || data[0];
			uni.uploadFile({
				url : uploadImage,
				filePath,
				name: 'file',
				header: {
					"x-api-key": _this.token,
					"merchant-id": 1
				},
				formData: {
					'access-token': _this.token,
					"merchant-id": 1
				},
				success (r) {
					uni.hideLoading();
					const data = JSON.parse(r.data);
					if (data.code === 200) {
						_this.profileInfo.head_portrait = data.data.url;
						_this.handleUpdateInfo(_this.profileInfo)
					} else {
						this.$api.msg('data.message')
					}
				}
			 });
		},
		// 监听日期更改
		bindDateChange(e) {
			this.date = e.target.value
		},
		// 数据初始化
		initData(){
		this.userInfo=uni.getStorageSync('userInfo')
		console.log(this.userInfo)
	    //this.token = uni.getStorageSync('accessToken') || undefined;
		this.token=this.userInfo.id
			if (!this.token) {
				uni.navigateTo({
					url: "/pages/public/login",
				})
			} else {
				this.getMemberInfo()
			}
		},
		// 获取用户信息
		async getMemberInfo () {
			await this.$get('http://localhost:8080/static/api/memberInfo.json').then(r => {
		    this.loading = false;
				this.profileInfo = r.data;
				this.date = this.profileInfo.birthday;
			}).catch(() => {
		    this.loading = false;
			})
		},
		// 更新用户信息
		async toUpdateInfo(e){
			const formData = e.detail.value;
			let rule = [
				{name:"mobile", checkType : "phoneno", checkRule:"11,11",  errorMsg:"请输入正确的手机号"}
			];
			const checkRes = graceChecker.check(formData, rule);
			if(!checkRes){
				this.$api.msg(graceChecker.error);
				return;
			}
			this.handleUpdateInfo(formData);
		},
		// 更新用户信息
		async handleUpdateInfo (formData) {
			this.loadProgress = this.loadProgress + 6;
	    const timer = setInterval(() => {
				this.loadProgress = this.loadProgress + 6;
	    }, 50);
			await this.$put(`${memberUpdate}?id=${this.profileInfo.id}`, {
				...formData,
				birthday: this.date
			}).then(() =>{
			    clearInterval(timer);
					this.loadProgress = 0;
			    this.$api.msg('恭喜您, 资料修改成功!');
			    setTimeout(() => {
				    uni.switchTab({
					    url: '/pages/user/user'
				    })
			    }, 1 * 1000);
			});
		}
	}
}
